import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import Layout from "../components/layout"
import SEO from "../components/seo"

const Blog = () => {
    const data = useStaticQuery(graphql`
        query {
            allContentfulBlog(sort: { fields: publishedDate, order: DESC }) {
                edges {
                    node {
                        id
                        title
                        slug
                        thumbnail {
                            fluid {
                                ...GatsbyContentfulFluid
                            }
                        }
                        excerpt {
                            excerpt
                        }
                        publishedDate(fromNow: true)
                    }
                }
            }
        }
    `)
    return (
        <Layout>
            <SEO title="Blog" />
            <div className="blog">
                <label className="title">Blog Archive</label>
                <ul className="row">
                    {data.allContentfulBlog.edges.map(edge => (
                        <li className="column-1" key={edge.node.id}>
                            <Link
                                to={`/blog/${edge.node.slug}`}
                                className="box"
                            >
                                <div className="body">
                                    <div className="heading">
                                        {edge.node.title}
                                    </div>
                                    <p className="excerpt">
                                        {edge.node.excerpt.excerpt.slice(
                                            0,
                                            100
                                        )}
                                        {`...`}
                                    </p>
                                    <span className="date">
                                        {edge.node.publishedDate}
                                    </span>
                                </div>
                                <div className="thumbnail">
                                    <Img fluid={edge.node.thumbnail.fluid} />
                                    {/* {console.log(edge.node.thumbnail.fluid)} */}
                                </div>
                            </Link>
                        </li>
                    ))}
                </ul>
            </div>
        </Layout>
    )
}
export default Blog
